  width:9vh;
}
@-webkit-keyframes App-logo-spin {
  from {
    margin-top: 0px;
  }
  to {
    margin-top: 20px;
  }
}
@keyframes App-logo-spin {
  from {
    margin-top: 0px;
  }
  to {
    margin-top: 20px;
  }
}
@-webkit-keyframes App-logo-spin2 {
  from {
    margin-bottom: 20px;
  }
  to {
    margin-bottom: 0  px;
  }
}
@keyframes App-logo-spin2 {
  from {
    margin-bottom: 20px;
  }
  to {
    margin-bottom: 0  px;
  }
}

